import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

const count = new Vuex.Store({
  state: {
    count: [0],
  },
  getters: {
    getCount(state) {
      return state.count;
    }
  },
  mutations: {
    set(state, {type}) {
      state.count[type.id] = type.value;
    }
  },
  actions: {
    setCount({ commit }, query) {
      commit('set', {type: query});
    }
  },
});

export default count;


