<template>
  <div class="calc-block container"
       v-if="product !== null">
    <div class="calc-flex">

      <div v-for="id in list"
           :class="['calc-left', (currentID === id)? 'active': 'd-none']">
        <app-LeftBlock v-if="loading"
                       :id="id"
                       :product="product[id]"></app-LeftBlock>
      </div>


      <div class="calc-right">
        <div v-for="id in list">
          <app-list :id="id"
                    :loading="loading"
                    :product="product[id]">
          </app-list>
        </div>

        <app-AddNew></app-AddNew>

        <form action="https://loans.tinkoff.ru/api/partners/v1/lightweight/create"
              @submit="checkForm"
              method="post"
              target="_blank"
              ref="form">


          <div v-for="id in list"
               :class="currentID === id? 'active': 'd-none'">
            <div v-if="objName[id - 1]">
              <input type="hidden"
                     :value="objName[id - 1]"
                     :name="'itemName_' + (Number(id) - 1)">
            </div>

            <app-RightBlock :product="product[id]"
                            :id="id"></app-RightBlock>
          </div>

          <app-SubmitButton :price="product[currentID].PRICE"></app-SubmitButton>


        </form>

      </div>

    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  import LeftBlock from './Calculator/LeftBlock';
  import RightBlock from './Calculator/RightBlock';
  import List from './Calculator/ProductList';
  import AddNew from './Calculator/AddNew';
  import SubmitButton from './Calculator/SubmitButton';

  import {eventEmitter} from "./main";

  export default {
    name: 'app',
    data() {
      return {
        product: null,
        list: 1,
        defaultName: 'Кровать серии ГАРМОНИЯ 1690*900',
        currentID: 1,
        loading: false,
        objName: {},
      }
    },
    mounted() {
      this.getProduct(this.defaultName);

      eventEmitter.$on('productSelected', (data) => {
        console.log(data);
        this.currentID = data.id;
        this.getProduct(data.name, data.id);
      });

      eventEmitter.$on('productAdded', () => {
        this.getProduct(this.defaultName, null, true);
      });

      eventEmitter.$on('tabChanged', (id) => {
        this.currentID = id;
      });

    },
    methods: {
      checkForm(e) {
        this.loading = false;
        const formData = new FormData(this.$refs['form']);
        let array = [];
        let firstKey = null,
          prevText = null,
          m = 0;
        let data = {};
        for (let [key, val] of formData.entries()) {
          array = (key.split('|'));
          if (array.length === 2) {
            if(data[array[0]])
              data[array[0]] += '; ' + val;
            else
              data[array[0]] = val;
          }
          else if (array.length === 3) {
            data[array[0]] += ' [' + val + ']';
          }
          else {
            if (key.indexOf('itemName') === -1) {
              data[key] = val;
            }
          }
          // Object.assign(data, {[key]: val});
        }

        e.preventDefault();
        let key,
          n = 0;

        console.log(data);
        for (key in data) {
          if (key.indexOf('itemName') !== -1) {
            this.objName[n] = data[key];
            n++;
          }
        }

        console.log(this.objName);
        this.loading = true;
        let form = this.$refs['form'];
        setTimeout(() => form.submit(), 100);

      },

      getProduct(name, id = null, addToList = false) {
        this.loading = false;
        let selectedID = this.list;

        if (addToList)
          selectedID = this.list + 1;

        axios
          .post('/api.php', {
            key: name
          })
          .then(response => {
            if (this.product === null) {
              this.product = [];
            }

            if (id !== null)
              selectedID = id;

            this.product[selectedID] = response.data;

            eventEmitter.$emit('productDownloaded', {id: selectedID, product: this.product[selectedID]});


            if (addToList) {
              this.list++;
            }

            this.loading = true;

          })
          .catch(error => console.log(error));
      }
    },
    components: {
      appLeftBlock: LeftBlock,
      appRightBlock: RightBlock,
      appList: List,
      appAddNew: AddNew,
      appSubmitButton: SubmitButton,
    },
  }
</script>

<style>
  @import "/css/bootstrap.min.css";
  @import "/css/template.css";
</style>


