<template>
  <div class="calc__photo-active">
    <img :src="newPhoto? newPhoto: photo"
         :title="name"
         class="calc__photo-activeImg">
  </div>
</template>

<script>
  import store from './store/index';
  import {eventEmitter} from "../../main";

  export default {
    name: "MainPhoto",
    props: ['photo', 'name', 'photos', 'id'],
    data() {
      return {
        newPhoto: null,
      }
    },
    mounted() {
      eventEmitter.$on('tabChanged', (id) => {
        if(this.id === id) {
          this.newPhoto = this.photo;
        }
      });

      eventEmitter.$on('photoUpdated', (key) => {
        this.newPhoto = this.photos[key];
      });
    },

  }
</script>

<style scoped>

</style>


