<template>
  <div>
    <p class="calc__price-text">Цена: <span class="calc__price-original">{{ getOldPrice }} руб.</span></p>
    <p class="calc__price-discount__val">{{ getNewPrice }} руб.</p>
    <input :name="'itemPrice_' + (Number(id) - 1)"
           :value="getNewPrice / this.count"
           type="hidden">
  </div>
</template>

<script>
  import {eventEmitter} from "../main";

  export default {
    name: "ProductSum",
    props: {
      price: Number,
      id: Number,
    },
    data() {
      return {
        count: 1,
        priceOptions: 0,
        discountVal: 0.2,
      };
    },
    computed: {
      getNewPrice() {
        let price = this.price * (1 - this.discountVal) * this.count + this.priceOptions * this.count;
        eventEmitter.$emit('priceChanged', {id: this.id, price: price});

        return price;
      },
      getOldPrice() {
        return this.price * this.count + this.priceOptions * this.count;
      }
    },
    mounted() {

      eventEmitter.$on('countUpdated', (data) => {
        if (data === this.id) {
          this.count = this.$store.getters.getCount[data];
        }
      });

      eventEmitter.$on('addToSum', (data) => {
        if (data.id === this.id) {
          this.priceOptions += data.price;
        }

      });
    }
  }
</script>

<style scoped>

</style>


