<template>
  <div>
    <app-MainPhoto :photo="product.PHOTO"
                   :name="product.NAME"
                   :id="id"
                   :photos="product.PHOTOS"></app-MainPhoto>
    <app-ListPhoto :photos="product.PHOTOS"
                   :name="product.NAME"></app-ListPhoto>
    <app-Description :arDescription="product.DESCRIPTION"></app-Description>
  </div>
</template>

<script>
  import MainPhoto from './LeftBlock/MainPhoto';
  import ListPhoto from './LeftBlock/ListPhoto';
  import Description from './LeftBlock/Description';
  import {eventEmitter} from "../main";

  export default {
    name: "LeftBlock",
    props: ['product', 'id'],
    data() {
      return {
        currentId: 1,
      }
    },
    created() {
    },
    components: {
      appMainPhoto: MainPhoto,
      appListPhoto: ListPhoto,
      appDescription: Description,
    },
  }
</script>

<style scoped>

</style>


