import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    src: '',
  },
  getters: {
    getSrc(state) {
      return state.src;
    }
  },
  mutations: {
    set(state, {type}) {
      state.src = type;
    }
  },
  actions: {
    setSrc({ commit }, query) {
      commit('set', {type: query});
    }
  },
});

export default store;


