<template>
  <div class="addButton-block">
    <button class="addButton-button" @click="addNewProduct" type="button">Добавить</button>
  </div>
</template>

<script>
  import {eventEmitter} from '../main';

  export default {
    name: "AddNew",
    data() {
      return {

      }
    },
    methods: {
      addNewProduct() {
        eventEmitter.$emit('productAdded');
      }
    },
  }
</script>

<style scoped>

