<template>
  <div class="calc__options-item">
    <div class="calc__options-item__top">
      <label class="calc__options-item__top-label">
        <p>{{ data.NAME }}</p>
        <input :name="'itemName_' + (Number(id) - 1) + '|' + data.NAME"
               type="checkbox"
               @change="addToSum"
               :value="data.NAME">
        <div class="checkbox__controller"></div>
      </label>
    </div>
    <transition name="fade">
      <div data-next="block"
           v-if="isShow"
           class="calc__options-item__hiddenInput">
        <input :name="'itemName_' + (Number(id) - 1) + '|' + data.NAME + '|text'"
               type="text"
               :title="name + ' - текст'"
               required
               :placeholder="'Введите - ' + data.NAME">
      </div>
    </transition>
  </div>
</template>

<script>
  import {eventEmitter} from "../../main";

  export default {
    name: "TypeInput",
    props: ['data', 'id', 'after', 'child', 'name'],
    data() {
      return {
        isShow: false,
        inputIsShow: true,
      }
    },
    methods: {
      addToSum() {
        this.isShow = !this.isShow;
        this.priceType = !this.priceType;
        let price = this.data.PRICE;

        if (!this.priceType)
          price *= (-1);

        eventEmitter.$emit('addToSum', {'id': this.id, 'price': price});

        if (this.child) {
          eventEmitter.$emit('parentUpdated', {'id': this.id, 'name': this.data.NAME});
        }
      }
    },
  }
</script>

<style scoped>
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }

  .fade-enter, .fade-leave-to {
    opacity: 0;
  }
</style>


