<template>
  <div class="calc__submit-block">
    <div class="calc__price-text">Общая сумма: <span class="final-sum">{{ finalPrice }} руб.</span></div>
    <input type="hidden"
           value="dardav"
           name="shopId">
    <input type="hidden"
           :value="finalPrice"
           name="sum">
    <button type="button"
            class="form-modal__button"
            data-target="modal"
            data-modal="#feedback">Заказать
    </button>
    <input type="submit"
           class="form-submit__buy"
           value="Купить в кредит">
  </div>
</template>

<script>
  import {eventEmitter} from "../main";

  export default {
    name: "SubmitButton",
    props: ['price'],
    data() {
      return {
        finalSum: {},
        finalPrice: this.price * 0.8,
      }
    },
    methods: {
      getFinalSum() {
        let price = 0,
          index = 0;
        let objPrice = this.finalSum;
        for (index in objPrice) {
          price += objPrice[index];
        }
        this.finalPrice = price;
      }
    },
    mounted() {
      eventEmitter.$on('priceChanged', (data) => {
        this.finalSum[data.id] = data.price;
        this.getFinalSum();
      });
    }
  }
</script>

<style scoped>

</style>


