<template>
  <div class="calc__options-item"
       v-if="inputIsShow">
    <div class="calc__options-item__top">
      <label class="calc__options-item__top-label">
        <p>{{ data.NAME }}</p>
        <input :name="'itemName_' + (Number(id) - 1) + '|' + data.NAME"
               type="checkbox"
               v-model="checked"
               @change="addToSum"
               :price-type="priceType"
               :value="data.NAME">
        <div class="checkbox__controller"></div>
      </label>
    </div>
  </div>
</template>

<script>
  import {eventEmitter} from "../../main";

  export default {
    name: "TypeDefault",
    props: ['data', 'id', 'after', 'child', 'name'],
    data() {
      return {
        priceType: false,
        inputIsShow: (this.after === undefined),
        checked: false,
        count: 1,
        lastCount: 0,
      }
    },
    methods: {
      addToSum() {
        this.priceType = this.checked;
        let price = 0;

        if(this.lastCount < this.count) {
          price = this.data.PRICE;
        }
        else {
          price = this.data.PRICE * (-1);
        }

        if (!this.priceType)
          price *= (-1);

        eventEmitter.$emit('addToSum', {'id': this.id, 'price': price});

        if (this.child !== undefined) {
          eventEmitter.$emit('parentUpdated', {'id': this.id, 'name': this.data.NAME});
        }

      },

      removeFromSum() {
        eventEmitter.$emit('addToSum', {'id': this.id, 'price': this.count * this.data.PRICE * (-1)});
      }
    },
    mounted() {
      if (this.after !== undefined) {
        eventEmitter.$on('parentUpdated', (data) => {
          if (data.name === this.after) {

            this.count = data.count;

            this.priceType = !data.isShow;
            this.inputIsShow = data.isShow;

            if(data.isShow && this.checked) {
              this.addToSum();
            }

            if(!this.inputIsShow && this.checked) {
              this.removeFromSum();
              this.checked = false;
            }
          }
        });
      }
    }
  }
</script>

<style scoped>

</style>


