<template>
  <div>

    <input type="hidden"
           :name="'itemName_' + (Number(id) - 1) + '|name'"
           :value="product.NAME">

    <keep-alive>
      <app-count :id="id"
                 :name="product.NAME"></app-count>
    </keep-alive>

    <div v-for="objProduct in product.OPTIONS">
      <keep-alive>
        <app-TypeInput v-if="(objProduct.TYPE === 'text')"
                       :data="objProduct"
                       :name="product.NAME"
                       :after="objProduct.AFTER"
                       :child="objProduct.CHILD"
                       :id="id"></app-TypeInput>

        <app-TypeDefault v-else-if="(objProduct.TYPE === 'default')"
                         :data="objProduct"
                         :name="product.NAME"
                         :after="objProduct.AFTER"
                         :child="objProduct.CHILD"
                         :id="id"></app-TypeDefault>

        <app-TypeCount v-else-if="(objProduct.TYPE === 'count')"
                       :data="objProduct"
                       :name="product.NAME"
                       :after="objProduct.AFTER"
                       :child="objProduct.CHILD"
                       :id="id"></app-TypeCount>
      </keep-alive>
    </div>

    <keep-alive>
      <app-ProductSum :id="id"
                      :price="product.PRICE">
      </app-ProductSum>
    </keep-alive>
  </div>
</template>

<script>
  import {eventEmitter} from "../main";
  import store from './TypeOptions/store/index';

  import List from '../Calculator/ProductList';
  import Count from '../Calculator/Count';
  import ProductSum from '../Calculator/ProductSum';
  import AddNew from '../Calculator/AddNew';
  import TypeInput from '../Calculator/TypeOptions/Input';
  import TypeDefault from '../Calculator/TypeOptions/Default';
  import TypeCount from '../Calculator/TypeOptions/Count';

  export default {
    name: "RightBlock",
    props: ['product', 'id'],
    data() {
      return {
        list: 1,
        currentId: 1,
        flag: false,
      }
    },
    methods: {
      option() {
        return true;
      }
    },
    created() {
      eventEmitter.$on('productAdded', () => {
        this.currentId = this.list;
      });

      eventEmitter.$on('tabChanged', (id) => {
        this.currentId = id;
      });
    },
    components: {
      appList: List,
      appCount: Count,
      appProductSum: ProductSum,
      appAddNew: AddNew,
      appTypeInput: TypeInput,
      appTypeDefault: TypeDefault,
      appTypeCount: TypeCount,
    },
    store,
  }
</script>

<style scoped>

</style>


