<template>
  <div class="calc__options-item"
       data-next="block"><p>Количество</p>
    <div class="calc__options-count" data-count>
      <button type="button"
              @click="changeCount('-1')">-
      </button>
      <input title="Количество"
             :name="'itemQuantity_' + (Number(id) - 1)"
             readonly="readOnly"
             :value="count">
      <button type="button"
              @click="changeCount('+1')">+
      </button>
    </div>
  </div>

</template>

<script>

  import {eventEmitter} from "../main";

  export default {
    props: ['id', 'name'],
    name: "Count",
    data() {
      return {
        count: 1,
      }
    },
    methods: {
      changeCount(val) {
        let newCount = this.count + Number(val);
        if(newCount > 0) {
          this.count = newCount;
          this.$store.dispatch('setCount', {value: this.count, id: this.id});
          eventEmitter.$emit('countUpdated', this.id);
        }
      },
    },
    ready: function() {
      this.changeCount();
    },
  }
</script>

<style scoped>

</style>


