<template>

  <div class="calc__select__list">

    <div class="calc__select__active-item"
         @click="isShow = !isShow">{{ product.NAME }}
    </div>

    <label class="calc__select-radio">
      <input type="radio"
             title="Переключить товар"
             :id="'tab' + id"
             name="calc_tab"
             @change="tabChange(id)"
             :checked="currentId === id"
             :value="'tab' + id">

      <div class="calc__select-controller"></div>
    </label>

    <div class="calc__select__list-block">
      <transition name="slide">
        <div v-if="isShow"
             class="calc__select__items-select">
          <div v-for="arProduct in product.LIST"
               class="calc__select__item"
               @click="productName(arProduct.NAME)"
               :select="arProduct.NAME">
            <img :src="arProduct.PREVIEW_PICTURE">
            <p>{{ arProduct.NAME }}</p></div>
        </div>
      </transition>
    </div>

  </div>

</template>

<script>
  import {eventEmitter} from "../main";

  export default {
    name: "ProductList",
    props: ['product', 'id', 'checked'],
    data() {
      return {
        isShow: false,
        currentId: 1,
      }
    },
    mounted() {
      eventEmitter.$on('productDownloaded', (data) => {
        if(this.id === data.id) {
          this.currentId = data.id;
        }
      });
    },
    methods: {
      tabChange(id) {
        console.log('changed');
        eventEmitter.$emit('tabChanged', id);
      },

      productName(name) {
        eventEmitter.$emit('productSelected', {name: name, id: this.id});
        this.isShow = false;
      },
    },
    destroyed() {
      console.log('destroy');
    }
  }
</script>

<style scoped>
  .slide-enter-active {
    -moz-transition-duration: 0.3s;
    -webkit-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -moz-transition-timing-function: ease-in;
    -webkit-transition-timing-function: ease-in;
    -o-transition-timing-function: ease-in;
    transition-timing-function: ease-in;
  }

  .slide-leave-active {
    -moz-transition-duration: 0.3s;
    -webkit-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -moz-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
    -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
    -o-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
    transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
  }

  .slide-enter-to, .slide-leave {
    max-height: 1000px;
    overflow: hidden;
  }

  .slide-enter, .slide-leave-to {
    overflow: hidden;
    max-height: 0;
  }
</style>


