<template>
  <div class="calc__options-item">
    <div class="calc__options-item__top">
      <label class="calc__options-item__top-label">
        <p>{{ data.NAME }}</p>
        <input :name="'itemName_' + (Number(id) - 1) + '|' + data.NAME"
               type="checkbox"
               @change="countFunc"
               :value="data.NAME">
        <div class="checkbox__controller"></div>
      </label>
    </div>
    <div class="calc__options-count"
         v-if="isShow">
      <button type="button"
              @click="changeCount(-1)">-
      </button>
      <div class="calc__options-value">{{ count }}</div>
      <input type="hidden"
             readonly="readOnly"
             data-type="count"
             :title="'Количество - ' + data.NAME"
             :name="'itemName_' + (Number(id) - 1) + '|' + data.NAME + '|count'"
             :value="count + ' шт'">
      <button type="button"
              @click="changeCount(+1)">+
      </button>
    </div>
  </div>
</template>

<script>
  import {eventEmitter} from "../../main";

  export default {
    name: "TypeCount",
    props: ['data', 'id', 'after', 'child', 'name'],
    data() {
      return {
        count: 1,
        isShow: false,
        priceType: false,
        lastCount: 0,
        inputIsShow: true,
      }
    },
    methods: {
      changeCount(val) {
        let newVal = this.count + val;
        if (newVal > 0) {
          this.lastCount = this.count;
          this.count = newVal;
          this.addToSum(true, false);
        }
      },

      addToSum(type = true, change = true) {
        let price = 0;

        //Проверять, уменьшили или увеличили количество
        if(this.lastCount < this.count) {
          price = this.data.PRICE;
        }
        else {
          price = this.data.PRICE * (-1);
        }

        //Вернуть цену, с учетом ранее поставленного количества
        if(type === false) {
          price = this.data.PRICE * this.count;
        }

        //Убрать всю стоимость, при выключении
        if(!this.priceType)
          price = (-1) * this.data.PRICE * this.count;

        eventEmitter.$emit('addToSum', {'id': this.id, 'price': price});


        //Показать скрытые опции
        if (this.child !== undefined) {
          eventEmitter.$emit('parentUpdated', {'id': this.id, 'name': this.data.NAME, 'count': this.count, isShow: this.isShow});
        }
      },

      countFunc() {
        // +/- для расчета
        this.priceType = !this.priceType;

        //Скрыть/показать поле для количества
        this.isShow = !this.isShow;
        this.addToSum(!this.priceType);

      }
    },
  }
</script>

<style scoped>

</style>


