import Vue from 'vue';
import Vuex from 'vuex';
import App from './App.vue';
import axios from 'axios';
import VueAxios from 'vue-axios';

Vue.use(VueAxios, axios);
Vue.use(Vuex);

export const eventEmitter = new Vue();

let app = new Vue({
  el: '#calculator',
  render: h => h(App)
});



