<template>
  <div class="calc__photo-items">
    <div class="calc__photo-items"
         v-for="(strPhoto, key) in photos">

      <div :class="'calc__photo-item' + (isActive === key? ' active': '')"
           @click="getNewMainPhoto(key)"
           :id="key">
        <img :src="strPhoto"
             class="calc__photo-item__img">
      </div>

    </div>
  </div>
</template>

<script>
  import store from './store/index';
  import {eventEmitter} from "../../main";

  export default {
    name: "ListPhoto",
    props: ['photos'],
    data() {
      return {
        isActive: 0,
      }
    },
    methods: {
      getNewMainPhoto(key) {
        this.isActive = key;
        eventEmitter.$emit('photoUpdated', key);
      }
    },
  }
</script>

<style scoped>

</style>


